@font-face {
  font-family: 'marvel';
  src: url("../Assets/marvel.ttf") format("opentype"); }

.comic {
  height: 100%;
  margin-bottom: 20px;
  box-shadow: 1px 1px 5px #333;
  border-radius: 3px;
  background-color: #f4f4f4; }

.cover {
  position: relative;
  top: -100%;
  height: 100%;
  -webkit-transition: all 1s ease;
  transition: transform 1s ease-in-out, box-shadow 1s, opacity 0.7s 0.3s ease;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  transform-origin: left center;
  z-index: 1; }

.comic .cover img {
  width: 100%;
  display: block;
  max-height: 100%;
  border-radius: 3px; }

.comic.turn .cover {
  transform: rotateY(90deg) skewY(-30deg);
  box-shadow: 50px 50px 80px #333;
  opacity: 0; }

.info {
  background: rgba(224, 189, 77, 0.4);
  height: 100%;
  font-size: 0.7rem;
  text-align: center;
  padding: 0.5rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(3, minmax(max-content, 20px));
  grid-gap: 0.5rem 0.3rem;
  overflow-y: scroll;
  font-family: 'Patrick Hand', cursive; }

.info a.comic-link {
  grid-column: 2 / 3; }

.info .comic-link {
  text-decoration: none; }

.info > .info-cont {
  border: 3px solid black; }

.info .descr {
  padding: 0.5rem;
  grid-column: span 2;
  display: flex;
  justify-content: center;
  align-items: center; }

.info::-webkit-scrollbar {
  display: none; }

.info .inf-title {
  font-size: 1.3rem;
  grid-column: span 2;
  overflow: hidden; }

.card img {
  width: 100%;
  max-height: 70%; }

.card {
  /* Add shadows to create the "card" effect */
  border-radius: 5px;
  overflow: hidden;
  position: relative;
  width: 100%; }

.card .desc {
  margin-bottom: 2rem; }

.card .front h4 {
  padding: 10% 1rem;
  font-size: 1.5rem; }

.card .front.no-image {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

/* On mouse-over, add a deeper shadow */
.card .front,
.card .back {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #fff;
  transition: transform 0.5s ease-in-out;
  backface-visibility: hidden;
  box-shadow: 0 0 8px black; }

.card.turn .front {
  transform: rotateY(180deg); }

.card .back {
  background: #333;
  color: #fff;
  overflow: hidden;
  padding: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  transform: rotateY(180deg); }

.card.turn .back {
  transform: rotateY(360deg); }

/* Add some padding inside the card container */
.container {
  padding: 0.5rem 1rem; }
  .container .item {
    margin-bottom: 0.5rem; }

.card.creator {
  background: #333;
  color: #fff;
  overflow: hidden;
  padding: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center; }
  .card.creator .container > h4:first-child {
    font-size: 1.5rem;
    margin-bottom: 2rem; }

.spinner::before {
  content: "";
  position: fixed;
  top: 34vh;
  left: 43vw;
  border-top: 5px solid #ff2b00;
  border-right: 5px solid transparent;
  border-radius: 50%;
  width: 15vmax;
  height: 15vmax;
  box-sizing: border-box;
  animation: spinner 0.7s linear infinite;
  z-index: 9999; }

.spinner::after {
  content: "";
  background: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  z-index: 9998; }

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-family: sans-serif; }

html,
body {
  font-family: Arial, Helvetica, sans-serif;
  background: #ccc;
  color: rgba(0, 0, 0, 0.98);
  height: 100vh; }

header {
